import { model, Schema, models } from 'mongoose';
import RolesModel from '../../roles/models/roles';

export default function userModel() {
  const collectionName = 'users';
  if (models[collectionName]) {
    return model(collectionName);
  }

  const userSchema = new Schema(
    {
      _id: {
        type: String,
        required: true,
      },
      userId: {
        type: String,
        required: true,
      },
      firstName: {
        type: String,
        required: true,
      },
      lastName: {
        type: String,
        required: true,
      },
      mobileNumber: {
        type: String,
        required: true,
      },
      email: {
        type: String,
      },
      profileImage: {
        type: String,
      },
      role: {
        type: Schema.Types.ObjectId,
        ref: RolesModel().modelName,
      },
      deviceToken: {
        type: String,
      },
      type: {
        type: String,
      },
      active: {
        type: Boolean,
        default: true,
      },
    },
    { timestamps: true }
  );

  return model(collectionName, userSchema);
}
